
import Header from "@/components/common/Header";
import CheckList from "@/components/common/CheckList";
import { setDefaultAddr } from "@/servers/api";
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: "",
props: [""],
data() {
return {
TitleObjData: {
titleContent: "收获地址",
showLeftBack: true,
showRightMore: false
}
};
},

components: {
Header,
CheckList,
XButton
},

computed: {
conHei() {
return { height: document.documentElement.clientHeight - 45 + "px" };
}
},

beforeMount() {},

mounted() {},

methods: {
addNewAddress() {
this.$router.push("/addnewaddress");
},
setDefAdd(val) {
this.setDefaultAddress(val);
},
setDefaultAddress(val) {
setDefaultAddr({
id: val[0]
})
.then(res => {
console.log(res);
if (res.result === 1) {
this.$vux.toast.show({
type: "success",
text: "设置成功",
time: 1000,
isShowMask: true
});
}
})
.catch(err => {
console.log(err);
});
}
},

watch: {}
};



